@import '../../style/variables.scss';


.char__search-form {
  padding: 25px;
  margin-top: 30px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
  position: relative;
  z-index: 5;
  background-color: #fff;
}

.char__search-label {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  color: #000;
}

.char__search-wrapper {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  input {
      padding: 0 10px;
      width: 250px;
      height: 38px;
      border: none;
      background: #FFFFFF;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      font-size: 14px;
  }
}

.char__search-error {
  margin-top: 25px;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  color: $main-color;
}

.char__search-critical-error {
  margin-top: 25px;
}

.char__search-success {
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  color: #03710E;
}